import React from 'react';

import PropTypes from 'prop-types';
import classNames from 'classnames';

import { withStyles } from '@material-ui/core/styles';
import CssBaseline from '@material-ui/core/CssBaseline';
import Drawer from '@material-ui/core/Drawer';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import List from '@material-ui/core/List';
import Typography from '@material-ui/core/Typography';
import Divider from '@material-ui/core/Divider';
import IconButton from '@material-ui/core/IconButton';
import Badge from '@material-ui/core/Badge';
import MenuIcon from '@material-ui/icons/Menu';
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';
import NotificationsIcon from '@material-ui/icons/Notifications';

import {connector} from "./store/utils/connector";
import MenuList from './MenuList/MenuList';

import News from "./sections/News/News";
import Reg from "./sections/Reg/Reg";
import Auth from "./sections/Auth/Auth";
import TestSection from "./sections/TestSection/TestSection";
import Accounts from "./sections/Accounts/Accounts";

const drawerWidth = 240;

const styles = theme => ({
    root: {
        display: 'flex',
    },
    toolbar: {
        paddingRight: 24, // keep right padding when drawer closed
    },
    toolbarIcon: {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'flex-end',
        padding: '0 8px',
        ...theme.mixins.toolbar,
    },
    appBar: {
        zIndex: theme.zIndex.drawer + 1,
        transition: theme.transitions.create(['width', 'margin'], {
            easing: theme.transitions.easing.sharp,
            duration: theme.transitions.duration.leavingScreen,
        }),
    },
    appBarShift: {
        marginLeft: drawerWidth,
        width: `calc(100% - ${drawerWidth}px)`,
        transition: theme.transitions.create(['width', 'margin'], {
            easing: theme.transitions.easing.sharp,
            duration: theme.transitions.duration.enteringScreen,
        }),
    },
    menuButton: {
        marginLeft: 12,
        marginRight: 36,
    },
    menuButtonHidden: {
        display: 'none',
    },
    title: {
        flexGrow: 1,
    },
    drawerPaper: {
        position: 'relative',
        whiteSpace: 'nowrap',
        width: drawerWidth,
        transition: theme.transitions.create('width', {
            easing: theme.transitions.easing.sharp,
            duration: theme.transitions.duration.enteringScreen,
        }),
    },
    drawerPaperClose: {
        overflowX: 'hidden',
        transition: theme.transitions.create('width', {
            easing: theme.transitions.easing.sharp,
            duration: theme.transitions.duration.leavingScreen,
        }),
        width: theme.spacing.unit * 7,
        [theme.breakpoints.up('sm')]: {
            width: theme.spacing.unit * 9,
        },
    },
    appBarSpacer: theme.mixins.toolbar,
    content: {
        flexGrow: 1,
        padding: theme.spacing.unit * 3,
        height: '100vh',
        overflow: 'auto',
    },
    chartContainer: {
        marginLeft: -22,
    },
    tableContainer: {
        height: 320,
    },
    h5: {
        marginBottom: theme.spacing.unit * 2,
    },
});

const App = ({state, dispatch, classes}) => {

    return (
        <div className={classes.root}>

            <CssBaseline />

            <AppBar
                position="absolute"
                color="default"
                className={classNames(classes.appBar, state.menuReducer.open && classes.appBarShift, 'box-shadow-0')}
            >
                <Toolbar disableGutters={!state.menuReducer.open} className={classes.toolbar}>
                    <IconButton
                        color="inherit"
                        aria-label="Open drawer"
                        onClick={() => dispatch.setter('menuReducer', {open: true})}
                        className={classNames(
                            classes.menuButton,
                            state.menuReducer.open && classes.menuButtonHidden,
                        )}
                    >
                        <MenuIcon />
                    </IconButton>
                    <Typography
                        component="h1"
                        variant="h6"
                        color="inherit"
                        noWrap
                        className={classes.title}
                    >
                        Контроль расходов
                    </Typography>
                    <IconButton color="inherit">
                        <Badge badgeContent={123} color="secondary">
                            <NotificationsIcon />
                        </Badge>
                    </IconButton>
                </Toolbar>
            </AppBar>
            <Drawer
                variant="permanent"
                classes={{paper: classNames(classes.drawerPaper, !state.menuReducer.open && classes.drawerPaperClose)}}
                open={state.menuReducer.open}
            >
                <div className={classes.toolbarIcon}>
                    <IconButton onClick={() => dispatch.setter('menuReducer', {open: false})}>
                        <ChevronLeftIcon />
                    </IconButton>
                </div>
                <Divider />
                <List>
                    <MenuList />
                </List>
            </Drawer>
            <main className={classes.content}>

                <div className={classes.appBarSpacer} />

                { state.menuReducer.select === 'news' ? <News /> : '' }
                { state.menuReducer.select === 'auth' ? <Auth /> : '' }
                { state.menuReducer.select === 'reg' ? <Reg /> : '' }
                { state.menuReducer.select === 'test' ? <TestSection /> : '' }
                { state.menuReducer.select === 'accounts' ? <Accounts /> : '' }

            </main>
        </div>
    );

}

App.propTypes = {
    classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(connector(App));